.bd-example,
.highlight {
  margin-right: 0;
  margin-left: 0;
}

.bd-example {
  border-width: .2rem;
  margin-bottom: 0;
}
.bd-example > .dropdown-menu:first-child {
  position: relative;
  top: auto;
  left: auto;
}

.doc-jumbotron {
  background-image: url("images/doc-jumbotron-bg.jpg");
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 7rem;
}
@media (max-width: 959px) {
  .doc-jumbotron h1 {
    font-size: 3.5rem;
    font-weight: 400;
    letter-spacing: -0.02em;
    line-height: 1.035715;
  }
}
@media (min-width: 1280px) {
  .navdrawer-permanent ~ .doc-jumbotron {
    margin-left: 17.5rem;
  }
}

@media (min-width: 1280px) {
  .navdrawer-permanent ~ .doc-main {
    margin-left: 17.5rem;
  }
}

.doc-navdrawer-body {
  min-height: 100%;
}

.doc-toolbar {
  position: fixed;
}
@media (min-width: 1280px) {
  .navdrawer-permanent ~ .doc-toolbar {
    margin-left: 17.5rem;
  }
}

